}

.steps-container .steps-header {
	font-size: 25px;
	margin-bottom: 30px;
}

.steps-container .contact-member {
	color: rgb(2, 66, 152);
}

.steps-container .step-label {
	font-size: 20px;
	font-family: bold;
}

.steps-container .step-desc {
	color: #6e6e6e !important;
	font-weight: bold;
}

.steps-container .css-8t49rw-MuiStepConnector-line {
	min-height: 50px !important;
}

.uc-container {
	position: relative;
	margin-top: 100px;
}

.uc-img {
	height: 400px;
	width: 500px;
}

.uc-header {
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.1;
	color: rgba(0, 0, 0, 0.8);
	text-shadow: 1px 1px 0 rgb(0 0 0 / 0%);
}

@-webkit-keyframes sway {
	0% {
		-webkit-transform: rotate(8deg);
		        transform: rotate(8deg);
	}
	50% {
		-webkit-transform: rotate(-8deg);
		        transform: rotate(-8deg);
	}
	100% {
		-webkit-transform: rotate(8deg);
		        transform: rotate(8deg);
	}
}

@keyframes sway {
	0% {
		-webkit-transform: rotate(8deg);
		        transform: rotate(8deg);
	}
	50% {
		-webkit-transform: rotate(-8deg);
		        transform: rotate(-8deg);
	}
	100% {
		-webkit-transform: rotate(8deg);
		        transform: rotate(8deg);
	}
}

.object {
	position: absolute;
	-webkit-animation: sway 2.4s infinite;
	        animation: sway 2.4s infinite;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
	-webkit-transform-origin: top;
	transform-origin: top;
	left: 0;
	top: 0;
	right: 50%;
	height: 10%;
	z-index: 999;
	text-transform: uppercase;
}

.object-shape {
	width: 100px;
	height: 100px;
	border-radius: 50%;
	display: block;
	background-color: #2187c7;
	margin: 0 auto;
	position: relative;
	color: #fff;
	text-align: center;
	padding-top: 25px;
	font-weight: 800;
	box-sizing: border-box;
}

.object-shape span {
	font-size: 22px;
	color: white;
}

.object-rope {
	height: 100%;
	width: 5px;
	background-color: #2187c7;
	content: "";
	display: block;
	margin-left: 50%;
}

.theme-bottom-border {
	width: 100%;
	height: 1px;
	position: absolute;
	top: 100%;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 3;
	background-color: rgba(0, 0, 0, 0.5);
}

.text-align-center {
	text-align: center;
}

