.uc-container {
	position: relative;
	margin-top: 100px;
}

.uc-img {
	height: 400px;
	width: 500px;
}

.uc-header {
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.1;
	color: rgba(0, 0, 0, 0.8);
	text-shadow: 1px 1px 0 rgb(0 0 0 / 0%);
}

@keyframes sway {
	0% {
		transform: rotate(8deg);
	}
	50% {
		transform: rotate(-8deg);
	}
	100% {
		transform: rotate(8deg);
	}
}

.object {
	position: absolute;
	animation: sway 2.4s infinite;
	animation-timing-function: ease-in-out;
	-webkit-transform-origin: top;
	-moz-transform-origin: top;
	transform-origin: top;
	left: 0;
	top: 0;
	right: 50%;
	height: 10%;
	z-index: 999;
	text-transform: uppercase;
}

.object-shape {
	width: 100px;
	height: 100px;
	border-radius: 50%;
	display: block;
	background-color: #2187c7;
	margin: 0 auto;
	position: relative;
	color: #fff;
	text-align: center;
	padding-top: 25px;
	font-weight: 800;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

.object-shape span {
	font-size: 22px;
	color: white;
}

.object-rope {
	height: 100%;
	width: 5px;
	background-color: #2187c7;
	content: "";
	display: block;
	margin-left: 50%;
}
